.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.highlighted {
  color: red;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#map { height: 60vh; width: 100%; }

.leaflet-container {
  width: 100%;
  height: 100%;
}

.leaflet-control-layers-list {
  display: inline-block;
  text-align: left;
}

#result-container {
  text-align: left;
}

#result-container>div {
  min-width: 150px;
  width: calc(48vw - 22px);
  float: left;
  padding: 5px;
  margin: 5px;
  border: 1px solid black;
}

#source-attribution {
  width: 100vw;
  clear: both;
  font-style: italic;
}